import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {  
		return {
		word:"<p>商家诉求大多相同、市场上的选择却如万花筒一般，美团服务市场上百家优质服务商入驻，共同为商家提供全品类服务。解决品牌、连锁、单店等各类商家经营真实需求。同时，美团服务市场也诚邀各垂直领域优质服务商入驻，合作共赢等你来站！</p><p>商家诉求大多相同、市场上的选择却如万花筒一般，美团服务市场上百家优质服务商入驻，共同为商家提供全品类服务。解决品牌、连锁、单店等各类商家经营真实需求。同时，美团服务市场也诚邀各垂直领域优质服务商入驻，合作共赢等你来站！</p>",
		showPopupMiddleImg:false,
		showPopupMiddleImg1:false,
		List:[],
		id:"",
		collection:1,
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		_self.getInfo()
	},
	methods: {
		apply:function(){
			this.showPopupMiddleImg1 = true
		},
		hidePopup:function(){
			this.showPopupMiddleImg = false
		},
		hidePopup1:function(){
			this.showPopupMiddleImg1 = false
		},
		getInfo:function(){
			var _self = this
			 if(!uni.getStorageSync("openId")||uni.getStorageSync("openId")==undefined||uni.getStorageSync("openId")===""){
				        uni.navigateTo({
				        	url:"../../load/load"
				        })
			}
			uni.request({ 
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:_self.id,pull:3,optionId:uni.getStorageSync("openId"),branch:0},
				method:"GET",
				success:function(res){
					_self.List = res.data[0]
					console.log(res.data)
					if(res.data[0].state === undefined || res.data[0].state===null){
						_self.collection = 1
					}else{
						_self.collection = 2
					}
				}
			})
		},
		collect:function(){
			var _self = this
			if(this.collection == 1){ 
				uni.request({
					url:_self.$api+"dockingManager/totalHideAdd",
					data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("收藏成功")
							_self.collection = 2 
						}
						else{
							uni.showToast({
								title:"收藏失败",
								icon:"none"
							})
						} 
					}
				})
			}else{
					uni.request({
					url:_self.$api+"dockingManager/totalHideDelete",
					data:{id:_self.id,optionId:uni.getStorageSync("openId")},
					success:function(res){
							if(res.data==1){
							console.log("取消收藏成功")
							_self.collection = 1
						}
						else{
							uni.showToast({
								title:"取消收藏失败",
								icon:"none"
							})
						}						
					},
					})
			}
		},
		/**
		 * 直接联系
		 */
		toContact:function(){
			var _self = this
			 uni.makePhoneCall({
				phoneNumber: _self.List.phone //仅为示例
				});
		},
		/**
		 * 转发
		 */
		onShareAppMessage: function (options) {
				var _self = this
				if (options.from === 'button') {
				  // 来自页面内转发按钮
				  console.log(options.target)
				}
				return {
				  //## 此为转发页面所显示的标题
				  //title: '好友代付', 
				  //## 此为转发页面的描述性文字
				  desc: _self.List.name, 
				  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
				  path:  'pages/serverPage/lawServerDetail/lawServerDetail?id='+_self.id, 
				  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
				  success: function(res) {
					  uni.request({
					  url:_self.$api+"dockingManager/activityHideAdd",
					  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
					  success:function(res){
						  console.log(res)
						  	console.log("转发成功")
					  },
					  }) 
				  },
				  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
				  fail: function() {
					  console.log("error")
				  }
				}
		},
	},
	 components: {uniPopup},
	
}
